import firebase from 'firebase/app'
import firebaseApp from './FirebaseConfig'

import 'firebase/firestore'
import getContentId from '../util/ContentId'

const db = firebase.firestore(firebaseApp)

function buildInsight(doc) {
  let data = doc.data()

  let i = {
    id: doc.id,
    deltaOps: data['o'],
    source: {
      label: data['s']['l'],
      link: data['s']['a']
    },
    created: data['c'] ? data['c'].toDate() : null,
    updated: data['u'] ? data['u'].toDate() : null
  }

  if (data['f'] !== undefined) {
    i.fragment = data['f']
  }

  return i
}

class Insights {

  getInsights(content, cb) {
    const cid = getContentId(content)
    db.collection('insights').where('cid', '==', cid).get().then(function (querySnapshot) {
      cb(querySnapshot.docs.map(buildInsight))
    }).catch(function (err) {
      console.error('Error fetching insights for content. cid: ', cid, err)
    })
  }

  saveInsight(insight) {
    let data = {
      cid: getContentId(insight.content),
      o: insight.deltaOps,
      s: {
        l: insight.source.label,
        a: insight.source.link
      },
      u: firebase.firestore.FieldValue.serverTimestamp()
    }

    if (insight.fragment !== undefined) {
      data.f = insight.fragment
    }

    if (insight.id === undefined) {
      // CREATE
      data.c = firebase.firestore.FieldValue.serverTimestamp()
      return db.collection('insights').add(data)
    }
    else {
      // UPDATE
      data.c = insight.created
      return db.collection('insights').doc(insight.id).set(data)
    }
  }

  deleteInsight(insight) {
    return db.collection('insights').doc(insight.id).delete()
  }

}

export default Insights
