import React from 'react'
import ReactModal from 'react-modal'

import InsightEditor from './InsightEditor'
import SingleInsight from './SingleInsight'

function InsightEditorModal({ editing, content, verse, insight, setInsight, toggle }) {

  return (
    <ReactModal
      isOpen={editing}
      onRequestClose={() => toggle()}>
      <div className="row">
        <div className="col-lg-6 col-md-6 col-sm-12">
          <h4>Edit Insight</h4>
          <hr />
          <InsightEditor
            content={content}
            verse={verse}
            insight={insight}
            setInsight={setInsight}
            toggle={toggle} />
        </div>
        <div className="col-lg-6 col-md-6 col-sm-12">
          <h4>Preview</h4>
          <hr />
          <SingleInsight insight={insight} />
        </div>
      </div>
    </ReactModal>
  )
}

export default InsightEditorModal
