import PropTypes from 'prop-types'
import React from 'react'
import { QuillDeltaToHtmlConverter } from 'quill-delta-to-html'
import parse from 'html-react-parser'

import VerseHighlightContext from './VerseHighlightContext'

function SingleInsight({ insight }) {
  const highlightContext = React.useContext(VerseHighlightContext)

  let source = insight.source.label
  if (insight.source.link !== undefined && insight.source.link !== '') {
    source = <a className="text-danger" href={insight.source.link} target="_blank" rel="noopener noreferrer">{insight.source.label}</a>
  }

  function updateHighlight(start, end) {
    if (insight.fragment) {
      highlightContext.updateHighlight(start, end)
    }
  }

  const insightHtml = new QuillDeltaToHtmlConverter(insight.deltaOps, {}).convert()

  return (
    <div className="insight m-1 p-1"
      onMouseEnter={() => insight.fragment ? updateHighlight(insight.fragment[0], insight.fragment[1]) : null}
      onMouseLeave={() => updateHighlight(0, 0)}>
      <div className="text-justify">
        {source && <span className="text-danger pr-2">({source})</span>}
        {parse(insightHtml)}
        {insight.created && <span className="text-secondary small float-right">{insight.created.getMonth() + 1}/{insight.created.getDate()}/{insight.created.getFullYear()}</span>}
        <div className="clearfix" />
      </div>
    </div>
  )
}

SingleInsight.propTypes = {
  insight: PropTypes.object.isRequired
}

export default SingleInsight
