import React from 'react'
import Quill from 'quill'

import PropTypes from 'prop-types'
import Insights from '../firebase/Insights'
import FragmentSelector from './FragmentSelector'
import SingleVerse from './SingleVerse'

function InsightEditor({ content, verse, insight, setInsight, toggle }) {
  const editorRef = React.createRef()

  const [isSubmitting, setSubmitting] = React.useState(false)

  const insights = new Insights()

  function handleSubmit(event) {
    setSubmitting(true)

    insight.content = content
    insights.saveInsight(insight).then(function () {
      setSubmitting(false);
      toggle()
    }).catch(function (err) {
      console.error('An error occurred while saving insight: ', err)
      setSubmitting(false);
    })

    event.preventDefault()
  }

  React.useEffect(() => {
    let quill = new Quill(editorRef.current, {
      theme: 'snow',
      placeholder: 'Record the insight here...'
    })

    if (insight.deltaOps) {
      quill.setContents(insight.deltaOps)
    }

    quill.on('text-change', function (d, od, s) {
      setInsight(i => ({ ...i, deltaOps: quill.getContents().ops }))
    });

    return () => {
      // No cleanup necessary for Quill.
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <div>
      <form onSubmit={handleSubmit}>
        {verse && <label>Verse</label>}
        {verse && <SingleVerse verse={verse} />}

        {verse && <FragmentSelector insight={insight} setInsight={setInsight} text={verse.text} />}

        <div className="form-group">
          <label htmlFor="source-label">Source Label</label>
          <input type="text" id="source-label" className="form-control" required
            value={insight.source.label}
            onChange={event => setInsight({ ...insight, source: { ...insight.source, label: event.target.value } })} />
        </div>

        <div className="form-group">
          <label htmlFor="source-link">Source Link</label>
          <input type="url" id="source-link" className="form-control"
            value={insight.source.link}
            onChange={event => setInsight({ ...insight, source: { ...insight.source, link: event.target.value } })} />
        </div>

        <div className="form-group">
          <label htmlFor="insight-text">Insight</label>
          <div ref={editorRef} id="insight-text"></div>
        </div>

        <button type="submit" className="btn btn-success float-right" disabled={isSubmitting}>Save</button>
        <button type="button" className="btn btn-danger mr-3 float-right" onClick={toggle} disabled={isSubmitting}>Cancel</button>
      </form>
    </div>
  )
}

InsightEditor.propTypes = {
  content: PropTypes.array.isRequired,
  verse: PropTypes.object,
  insight: PropTypes.object.isRequired,
  setInsight: PropTypes.func.isRequired,
  toggle: PropTypes.func.isRequired
}

export default InsightEditor
