import firebase from 'firebase/app'
import firebaseApp from './FirebaseConfig'
import uuidv4 from 'uuid/v4'

import 'firebase/firestore'
import 'firebase/storage'
import getContentId from '../util/ContentId'

const storage = firebase.storage(firebaseApp)
const db = firebase.firestore(firebaseApp)


function buildFile(doc) {
  let data = doc.data()

  return storage
    .ref('uploads/' + data['uuid'] + '/' + data['n'])
    .getDownloadURL()
    .then(function (url) {
      return {
        id: doc.id,
        uuid: data['uuid'],
        name: data['n'],
        extension: data['e'],
        scriptureTags: data['cids'],
        url: url,
        size: data['b'],
        created: data['c'] ? data['c'].toDate() : null,
        updated: data['u'] ? data['u'].toDate() : null
      }
    })
}

class Files {

  getFiles(content) {
    const cid = getContentId(content)
    return db
      .collection('uploads')
      .where('cids', 'array-contains', cid)
      .where('s', '==', 1)
      .get()
      .then(function (querySnapshot) {
        return Promise.all(querySnapshot.docs.map(buildFile))
      }).catch(function (err) {
        console.error('Error fetching files for content. cid:', cid, err)
      })
  }

  saveFile(fileObj, metadata, progressCb, cb) {
    const uuid = uuidv4()

    // 1. Save DB record with incomplete upload status
    let fileData = {
      uuid: uuid,
      n: metadata.name,    // Name, including extension
      s: 0,                // Status: uploading
      b: fileObj.size,     // Bytes
      e: metadata.extension,
      cids: metadata.scriptureTags,
      c: firebase.firestore.FieldValue.serverTimestamp(),
      u: firebase.firestore.FieldValue.serverTimestamp()
    }

    db.collection('uploads').add(fileData).then(function (dbRef) {
      // 2. Upload file
      const storageRef = storage.ref('uploads/' + uuid + '/' + metadata.name)

      var uploadTask = storageRef.put(fileObj)
      uploadTask.on('state_changed', function (snapshot) {
        progressCb(0 | snapshot.bytesTransferred / snapshot.totalBytes * 100)
      }, function (error) {
        console.log('Error while uploading file. ', fileData, error)
        cb(null, error)
      }, function () {
        dbRef.update({
          s: 1
        }).then(cb).catch(function (error) {
          console.error("Error while finalizing upload: ", error)
          cb(null, error)
        })
      })
    }).catch(function (error) {
      console.error("Error while precommitting upload: ", error)
      cb(null, error)
    })
  }

}

export default Files
