import React from 'react'

import SingleInsight from './SingleInsight'

function EditableInsight({ user, verse, insight, toggle, setInsight }) {

  function editInsight() {
    setInsight(insight)
    toggle()
  }

  const btn = <button
    key={'edit-' + insight.id}
    type="button"
    className="btn btn-success float-right ml-2"
    onClick={editInsight}>Edit Insight</button>

  return (
    <li key={insight.id}>
      <SingleInsight key={insight.id} verse={verse} insight={insight} />
      {user.status === 'LOGGED_IN' ? btn : null}
      <div style={{ clear: 'both' }}></div>
    </li>
  )
}

export default EditableInsight
