import React from 'react'

import LoadingBar from '../LoadingBar'
import Scriptures from '../../firebase/Scriptures'

function buildButton(label) {
  const classes = "text-capitalize text-white btn btn-success btn-lg"
  const href = '#/scriptures/' + label

  return (
    <div key={href} className="col-lg-4 col-md-4 col-sm-12 text-center text-nowrap p-3">
      <a href={href} role="button" className={classes}>{label.replace(/_/g, ' ')}</a>
    </div>
  )
}

function VolumeListPage() {
  const [volumes, setVolumes] = React.useState([])
  const scriptures = new Scriptures()

  React.useEffect(() => {
    scriptures.getVolumes(setVolumes)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  let content;
  if (volumes.length === 0) {
    content = <LoadingBar />
  }
  else {
    content = volumes.map(buildButton)
  }

  return (
    <div>
      <h1>Scriptures</h1>

      <div className="row">
        {content}
      </div>
    </div>
  )
}

export default VolumeListPage
