function assemble(pieces) {
  return pieces.join('/')
}

function getContentId(content) {
  let pieces = []

  if (content[0] !== undefined) {
    pieces.push(content[0].split('_').map(s => s[0]).join(''))
  }
  else return assemble(pieces)

  if (content[1] !== undefined) {
    pieces.push(content[1].split(/[_-]+/).map(s => s.substring(0, 3)).join('-'))
  }
  else return assemble(pieces)

  if (content[2] !== undefined) {
    pieces.push(content[2])
  }
  else return assemble(pieces)

  if (content[3] !== undefined) {
    pieces.push(content[3])
  }

  return assemble(pieces)
}

export default getContentId
