import React from 'react'
import { HashRouter, Route, Switch, Redirect } from 'react-router-dom'

import AboutPage from './pages/AboutPage'
import Background from './Background'
import BookListPage from './pages/BookListPage'
import Breadcrumb from './Breadcrumb'
import ChapterListPage from './pages/ChapterListPage'
import ChapterTextPage from './pages/ChapterTextPage'
import Footer from './Footer'
import HomePage from './pages/HomePage'
import InsightListPage from './pages/InsightListPage'
import LoginPage from './pages/LoginPage'
import Navbar from './Navbar'
import NotFoundPage from './pages/NotFoundPage'
import UnauthorizedPage from './pages/UnauthorizedPage'
import useFirebaseUser from '../firebase/FirebaseUser'
import VolumeListPage from './pages/VolumeListPage'
import VerseHighlightContext from './VerseHighlightContext'

function ComingSoonPage() {
  return (
    <header className="coming-soon">
      <h1 className="py-4 font-weight-bold">Enjoy the Scriptures</h1>
      <p className="py-4 font-weight-bold">Coming Soon</p>
    </header>
  )
}

function App() {
  const userState = useFirebaseUser()
  const [highlight, setHighlight] = React.useState({ first: 0, last: 0 })

  const contextState = {
    highlight,
    updateHighlight: (f, l) => setHighlight({ first: f, last: l })
  }

  if (userState.status === 'LOGGED_OUT') {
    return (
      <HashRouter basename='/'>
        <div className="App">
          <Switch>
            <Route exact path="/login" component={LoginPage} />
            <Route component={ComingSoonPage} />
          </Switch>
        </div>
      </HashRouter>
    )
  }

  return (
    <HashRouter basename='/'>
      <div className="App">
        <Navbar />
        <Route path='/' component={Breadcrumb} />

        <Route exact path="/" component={Background} />

        <VerseHighlightContext.Provider value={contextState}>
          <div className="container py-5">
            <Switch>
              <Route exact path="/" component={HomePage} />
              <Route exact path="/about" component={AboutPage} />

              {/* Scripture Notebook */}
              <Route exact path="/scriptures" component={VolumeListPage} />
              <Route exact path="/scriptures/:volume" component={BookListPage} />
              <Route exact path="/scriptures/:volume/:book" component={ChapterListPage} />
              <Route exact path="/scriptures/:volume/:book/:chapter" component={ChapterTextPage} />
              <Route exact path="/scriptures/:volume/:book/:chapter/:verse" component={InsightListPage} />

              {/* Topical Index */}
              <Route exact path="/topics" />

              {/* Login Related */}
              <Route exact path="/login" component={LoginPage} />
              <Route exact path="/unauthorized" component={UnauthorizedPage} />

              {/* 404 */}
              <Route exact path="/not_found" component={NotFoundPage} />
              <Redirect to="/not_found" />
            </Switch>
          </div>
        </VerseHighlightContext.Provider>

        {/* Invisble placeholder to prevent the sticky footer from covering content */}
        <div className="my-5"></div>

        <Footer />
      </div>
    </HashRouter>
  );
}

export default App
