import React from 'react'

import DropdownTreeSelect from 'react-dropdown-tree-select'
import LoadingBar from './LoadingBar'
import Scriptures from '../firebase/Scriptures'
import getContentId from '../util/ContentId'

function getLabel(id) {
  return id.replace(/_/g, ' ').replace(/(?:^|\s|-)\w/g, c => c.toUpperCase())
}

function buildDataTree(initialCid, scriptures, setData) {
  let d = []
  scriptures.getVolumes(volumes => {
    volumes.forEach(v => {
      let bookChildren = []
      scriptures.getBooks(v, books => {
        books.forEach(b => {
          let chapterChildren = []
          scriptures.getChapterCount(v, b, chapterCount => {
            for (let c = 1; c <= chapterCount; c++) {
              let verseChildren = []
              scriptures.getVerseCount(v, b, c, verseCount => {
                for (let n = 1; n <= verseCount; n++) {
                  let cid = getContentId([v, b, c, n])
                  let child = {
                    label: getLabel(b + ' ' + c + ':' + n),
                    value: cid
                  }

                  if (initialCid === cid) {
                    child['checked'] = true
                  }

                  verseChildren.push(child)
                }

                let cid = getContentId([v, b, c])
                let child = {
                  label: getLabel(b + ' ' + c),
                  value: cid,
                  children: verseChildren
                }

                if (initialCid === cid) {
                  child['checked'] = true
                }

                chapterChildren.push(child)
              })
            }
          })

          bookChildren.push({
            label: getLabel(b),
            value: getContentId([v, b]),
            children: chapterChildren
          })
        })
      })

      d.push({
        label: getLabel(v),
        value: getContentId([v]),
        children: bookChildren
      })
    })

    setData(d)
  })
}

function ScriptureTagSelector({ content, onScriptureTagChange }) {

  const scriptures = new Scriptures()
  const [data, setData] = React.useState(null)

  const cid = getContentId(content)

  React.useEffect(() => {
    buildDataTree(cid, scriptures, setData)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const onChange = (_, selectedNodes) => {
    onScriptureTagChange(selectedNodes.map(n => n.value))
  }

  // Because I don't yet see how you're supposed to manage state with this component,
  // I have to bootstrap the initial state myself.
  onScriptureTagChange([cid])

  return (
    <>
      {data ?
        <DropdownTreeSelect
          className="tree-select"
          texts={{ placeholder: 'Search' }}
          data={data}
          onChange={onChange}
          clearSearchOnChange={true} /> :
        <LoadingBar />}
    </>
  )
}

export default ScriptureTagSelector
