class Verse {
  constructor(volumeName, bookName, chapter, number, text) {
    this.volume = volumeName
    this.book = bookName
    this.chapter = chapter
    this.n = number
    this.text = text
  }

  getHref() {
    return ['/scriptures', this.volume, this.book, this.chapter, this.n].join('/')
  }

  getContent() {
    return [this.volume, this.book, this.chapter, this.n]
  }
}

export default Verse
