import React from 'react'
import PropTypes from 'prop-types'

import LoadingBar from './LoadingBar'
import Verse from '../model/Verse'
import groupInsights from '../util/InsightGroups'
import InsightGroup from './InsightGroup'

function InsightList({ verse, insights, toggle, setInsight }) {

  let insightContent
  if (insights === undefined) {
    insightContent = <LoadingBar />
  }
  else {
    // Sort insights by fragment
    const insightGroups = groupInsights(insights)
    insightContent = insightGroups.map(function (ig, idx) {
      return <InsightGroup key={idx} verse={verse} insightGroup={ig} toggle={toggle} setInsight={setInsight} />
    })
  }

  return (
    <>
      {insightContent}
    </>
  )
}

InsightList.propTypes = {
  verse: PropTypes.instanceOf(Verse).isRequired
}

export default InsightList
