import firebase from 'firebase/app'
import firebaseApp from './FirebaseConfig'

import 'firebase/firestore'
import Verse from '../model/Verse'
import LocalStorageCache from '../util/LocalStorageCache'
import getContentId from '../util/ContentId'

const db = firebase.firestore(firebaseApp)

const CACHE_TTL = 90 * 24 * 60 * 60 * 1000
const lsc = new LocalStorageCache().available() ? new LocalStorageCache() : null

function fetchVolumes(cb) {
  db.doc('scriptures/index').get().then(function (doc) {
    cb(doc.data()['volumes'])
  }).catch(function (err) {
    console.error('Error fetching volumes: ', err)
  })
}

function fetchBooks(volume, cb) {
  db.doc('scriptures/index/volumes/' + volume).get().then(function (doc) {
    cb(doc.data()['books'])
  }).catch(function (err) {
    console.error('Error fetching books for volume: ', volume, err)
  })
}

function fetchChapterCount(volume, book, cb) {
  db.doc('scriptures/index/volumes/' + volume + '/books/' + book).get().then(function (doc) {
    cb(doc.data()['chapters'])
  }).catch(function (err) {
    console.error('Error fetching chapter count for ' + volume + ', ' + book, err)
  })
}

function fetchVerseCount(volume, book, chapter, cb) {
  db.doc('scriptures/index/volumes/' + volume + '/books/' + book + '/chapters/' + chapter).get().then(function (doc) {
    cb(doc.data()['verses'])
  }).catch(function (err) {
    console.error('Error fetching verse count for ' + volume + ', ' + book + ', chapter ' + chapter, err)
  })
}

function fetchVerses(volume, book, chapter, cb) {
  let verses = {}

  db.collection('scriptures/index/volumes/' + volume + '/books/' + book + '/chapters/' + chapter + '/verses').get().then(function (querySnapshot) {
    querySnapshot.docs.forEach(function (doc) {
      verses[doc.id] = doc.data()['text']
    })

    cb(verses)
  }).catch(function (err) {
    console.error('Error fetching verses for ' + volume + ', ' + book + ', chapter ' + chapter, err)
  })
}

function buildVerseList(volume, book, chapter, verses) {
  return Object.keys(verses).map(function (n) {
    return new Verse(volume, book, chapter, n, verses[n])
  })
}

function curry(fn, ...args) {
  return (..._arg) => {
      return fn(...args, ..._arg);
  }
}

function cacheResult(cb, key, value) {
  lsc.setItem(key, value)
  cb(value)
}

class Scriptures {
  getVolumes(cb) {
    const key = 's/volumes'
    let volumes = lsc.getItem(key, CACHE_TTL)

    if (volumes !== null) {
      cb(volumes)
    }
    else {
      fetchVolumes(curry(cacheResult, cb, key))
    }
  }

  getBooks(volume, cb) {
    const key = 's/' + getContentId([volume])
    let books = lsc.getItem(key, CACHE_TTL)

    if (books !== null) {
      cb(books)
    }
    else {
      fetchBooks(volume, curry(cacheResult, cb, key))
    }
  }

  getChapterCount(volume, book, cb) {
    const key = 's/' + getContentId([volume, book])
    let chapterCount = lsc.getItem(key, CACHE_TTL)

    if (chapterCount !== null) {
      cb(chapterCount)
    }
    else {
      fetchChapterCount(volume, book, curry(cacheResult, cb, key))
    }
  }

  getVerseCount(volume, book, chapter, cb) {
    const key = 's/' + getContentId([volume, book, chapter])
    let verseCount = lsc.getItem(key, CACHE_TTL)
    if (verseCount !== null) {
      cb(verseCount)
    }
    else {
      fetchVerseCount(volume, book, chapter, curry(cacheResult, cb, key))
    }
  }

  getVerses(volume, book, chapter, cb) {
    this.getVerseCount(volume, book, chapter, function (verseCount) {
      let verseTexts = [...Array(verseCount).keys()].map(n => {
        const key = 's/' + getContentId([volume, book, chapter, n + 1])
        return lsc.getItem(key, CACHE_TTL)
      })

      if (!verseTexts.includes(null)) {
        let verses = {};
        [...Array(verseCount).keys()].forEach(n => {
          verses[n + 1] = verseTexts[n]
        })

        cb(buildVerseList(volume, book, chapter, verses))
      }
      else {
        fetchVerses(volume, book, chapter, function(verses) {
          for (const n in verses) {
            const key = 's/' + getContentId([volume, book, chapter, n])
            lsc.setItem(key, verses[n])
          }

          cb(buildVerseList(volume, book, chapter, verses))
        })
      }
    })
  }

  getVerse(volume, book, chapter, verse, cb) {
    this.getVerses(volume, book, chapter, function (verseList) {
      cb(verseList[verse - 1])
    })
  }
}

export default Scriptures
