import React from 'react'

import noUiSlider from 'nouislider'
import VerseHighlightContext from './VerseHighlightContext'

function FragmentSelector({text, insight, setInsight}) {
  const highlightContext = React.useContext(VerseHighlightContext)
  const sliderRef = React.createRef()

  React.useEffect(() => {
    const lastWord = text.split(' ').length

    const { current: sliderNode } = sliderRef
    noUiSlider.create(sliderNode, {
      start: insight.fragment,
      connect: true,
      range: {
        'min': 0,
        'max': lastWord
      },
      step: 1
    })

    sliderNode.noUiSlider.on('update', function (values, handle) {
      setInsight(i => ({ ...i, fragment: values.map(n => n | 0) })) // Force ints
      highlightContext.updateHighlight(values[0], values[1])
    })

    return () => {
      sliderNode.noUiSlider.destroy()
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <div className="form-group">
      <label htmlFor="phrase">Phrase Highlight</label>
      <div ref={sliderRef} />
    </div>
  )

}

export default FragmentSelector
