function compareInsightsByFragmentAndSource(a, b) {
  function compareIdx(a, b) {
    return a < b ? -1 : a > b ? 1 : 0
  }

  let r = compareIdx(a.fragment[0], b.fragment[0])
  if (r === 0) {
    r = compareIdx(a.fragment[1], b.fragment[1])
  }

  // If the fragments are equivalent, sort by the source label.
  if (r === 0) {
    r = a.source.label.localeCompare(b.source.label)
  }

  return r
}

function fragmentEq(f1, f2) {
  return f1[0] === f2[0] && f1[1] === f2[1]
}

function groupInsightsByFragment(insights) {
  if (insights.length === 0) {
    return insights
  }

  let groups = []

  let f = insights[0].fragment
  let g = []
  insights.forEach(i => {
    if (fragmentEq(f, i.fragment)) {
      g.push(i)
    }
    else {
      groups.push(Array.from(g))
      f = i.fragment
      g = [i]
    }
  })

  if (g.length !== 0) {
    groups.push(g)
  }

  return groups
}

function groupInsights(insights) {
  insights.sort(compareInsightsByFragmentAndSource)
  return groupInsightsByFragment(insights)
}

export default groupInsights
