import React from 'react'

import Files from '../firebase/Files'
import ScriptureTagSelector from './ScriptureTagSelector'

// Have to manage tag state external to React lifecycle
// https://github.com/dowjones/react-dropdown-tree-select/issues/32
let extScriptureTags = []

function FileUploader({ toggle, ...props }) {

  const files = new Files()

  const [data, setData] = React.useState({
    files: '',
    name: '',
    extension: ''
  })
  const [progress, setProgress] = React.useState(0)
  const [isSubmitting, setSubmitting] = React.useState(false)

  function onScriptureTagChange(tags) {
    extScriptureTags = tags
  }

  function handleSubmit(event) {
    let localTags = extScriptureTags
    extScriptureTags = []

    setSubmitting(true)
    files.saveFile(data.file, {
      name: data.name,
      extension: data.extension,
      scriptureTags: localTags
    }, setProgress, function (data, error) {
      if (error) {
        console.error("Error while saving file: ", error)
      }
      setSubmitting(false)
      toggle()
    })

    event.preventDefault()
  }

  return (
    <div>
      <form onSubmit={handleSubmit}>

        <div className="form-group">
          <label htmlFor="source-label">File</label>
          <input
            type="file"
            id="file-item"
            className="form-control-file"
            onChange={e => {
              const segments = e.target.files[0].name.split('.')
              setData({
                ...data,
                file: e.target.files[0],
                extension: segments[segments.length - 1],
                name: e.target.files[0].name
              })
            }}
            required />
        </div>

        <div className="form-group">
          <label htmlFor="file-name">Display Name</label>
          <input
            type="text"
            id="file-name"
            className="form-control"
            value={data.name}
            onChange={e => {
              setData({ ...data, name: e.target.value })
            }}
            required />
          <small id="name-help" className="form-text text-muted">
            This name will be used in the attachments list.
          </small>
        </div>

        <div className="form-group">
          <label htmlFor="scripture-tags">Scripture Tags</label>
          <ScriptureTagSelector onScriptureTagChange={onScriptureTagChange} {...props} />
          <small id="scripture-tags-help" className="form-text text-muted">
            This file will be associated with these scripture references.
          </small>
        </div>

        <div className="form-group">
          <label htmlFor="topic-tags">Topic Tags</label>

          <small id="topic-tags-help" className="form-text text-muted">
            This file will be associated with these topics.
          </small>
        </div>

        <button type="submit" className="btn btn-success float-right" disabled={isSubmitting}>Save</button>
        <button type="button" className="btn btn-danger mr-3 float-right" onClick={toggle} disabled={isSubmitting}>Cancel</button>
        <div className="clearfix"></div>

        <div className="my-3"></div>
        {isSubmitting ?
          <div>
            <label>Upload Progress: {progress}%</label>
            <div className="progress">
              <div
                className="progress-bar progress-bar-striped progress-bar-animated"
                role="progressbar"
                aria-valuenow={progress}
                aria-valuemin="0"
                aria-valuemax="100"
                style={{ width: progress + '%' }} />
            </div>
          </div> : null}
      </form>
    </div>
  )
}

export default FileUploader
