import React from 'react'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';

import LoadingBar from '../LoadingBar'
import Scriptures from '../../firebase/Scriptures'
import SingleVerse from '../SingleVerse'
import Insights from '../../firebase/Insights'
import EditableInsight from '../EditableInsight'
import InsightEditorModal from '../InsightEditorModal'
import FileUploaderModal from '../FileUploaderModal'
import useFirebaseUser from '../../firebase/FirebaseUser'
import Files from '../../firebase/Files';
import FilesTable from '../FilesTable';

const DEFAULT_INSIGHT = {
  source: {
    label: '',
    link: ''
  },
  deltaOps: []
}

function ChapterTextPage({ match: { params: { volume, book, chapter } } }) {
  const user = useFirebaseUser()
  const content = [volume, book, chapter]

  const [verses, setVerses] = React.useState([])
  const [insights, setInsights] = React.useState(null)
  const [files, setFiles] = React.useState(null)

  const [editing, setEditing] = React.useState(false)
  const [uploading, setUploading] = React.useState(false)
  const [newInsight, setNewInsight] = React.useState(DEFAULT_INSIGHT)

  const scriptures = new Scriptures()
  const insightsStore = new Insights()
  const filesStore = new Files()

  React.useEffect(() => {
    scriptures.getVerses(volume, book, chapter, setVerses)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  React.useEffect(() => {
    insightsStore.getInsights(content, setInsights)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [editing])

  React.useEffect(() => {
    filesStore.getFiles(content).then(files => setFiles(files))
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [uploading])

  let chapterContent
  if (verses.length === 0) {
    chapterContent = <LoadingBar />
  }
  else {
    chapterContent = verses.map(v => <SingleVerse key={v.getHref()} verse={v} clickable={true} />)
  }

  let insightContent
  if (insights === null) {
    insightContent = <LoadingBar />
  }
  else {
    insightContent = insights.map(i => <EditableInsight
      key={i.id}
      user={user}
      insight={i}
      toggle={toggleEditing}
      setInsight={setNewInsight} />)
  }

  let filesContent
  if (files === null) {
    filesContent = <LoadingBar />
  }
  else {
    filesContent = <FilesTable files={files} />
  }

  function toggleEditing() {
    setEditing(!editing)
  }

  function addNewInsight() {
    setNewInsight(DEFAULT_INSIGHT)
    toggleEditing()
  }

  function toggleUploading() {
    setUploading(!uploading)
  }

  function addNewFile() {
    toggleUploading()
  }

  return (
    <div>
      {editing ? <InsightEditorModal
        editing={editing}
        content={content}
        insight={newInsight}
        setInsight={setNewInsight}
        toggle={toggleEditing} /> : null}

      {uploading ? <FileUploaderModal
        uploading={uploading}
        toggle={toggleUploading}
        content={content} /> : null}

      <Tabs>
        <div className="row">
          <div className="col-lg-6 col-md-6 col-sm-12">
            <h4 className="text-capitalize">{book.replace(/_/g, ' ')} {chapter}</h4>
          </div>
          <div className="col-lg-6 col-md-6 col-sm-12">
            <TabList>
              <Tab>
                Chapter Insights
                {user.status === 'LOGGED_IN' && <button className="ml-2 btn btn-success" onClick={addNewInsight}>Add</button>}
              </Tab>
              <Tab>
                Additional Resources
                {user.status === 'LOGGED_IN' && <button className="ml-2 btn btn-success" onClick={addNewFile}>Upload</button>}
              </Tab>
            </TabList>
            <div className="clearfix"></div>
          </div>
        </div>
        <div className="row">
          <div className="col-lg-6 col-md-6 col-sm-12">
            <hr />
          </div>
          <div className="col-lg-6 col-md-6 col-sm-12">
            {/* <hr /> */}
          </div>
        </div>
        <div className="row">
          <div className="col-lg-6 col-md-6 col-sm-12">
            {chapterContent}
          </div>
          <div className="col-lg-6 col-md-6 col-sm-12">
            <TabPanel>
              <ol>
                {insightContent}
              </ol>
            </TabPanel>
            <TabPanel>
              {filesContent}
            </TabPanel>
          </div>
        </div>
      </Tabs>
    </div>
  )
}

export default ChapterTextPage
